import axios from "axios";
interface APIEntry {
  API: string;
  Description?: string;
}

const baseUrl = "https://api.publicapis.org/entries";

const fetchBase = async (): Promise<APIEntry[]> => {
  const { data } = await axios.get(`${baseUrl}`);
  const listInfo = data.entries.slice(10, 26);
  return listInfo;
};
export const fetchData = async (): Promise<void> => {
  try {
    const data: APIEntry[] = await fetchBase();
    const localItems: APIEntry[] = JSON.parse(
      localStorage.getItem("contact") || "[]"
    );
    if (localItems.length === 0) {
      localStorage.setItem("contact", JSON.stringify(data));
    }
  } catch (err) {
    console.log(err);
  }
};

export const fetchTextChat = async (
  chatId: string,
  setLoading: Function,
  setList: Function
): Promise<void> => {
  try {
    setLoading(true);
    const data: APIEntry[] = await fetchBase();
    const filter = data.filter(({ API }) => API === chatId);
    setList(filter);
  } catch (err) {
    console.log(err);
  } finally {
    setLoading(false);
  }
};
